import React, { Component } from 'react';
import styled from 'styled-components';
import AppBar from './AppBar';
import CoinList from './CoinList';
import Search from './Search';
import Dashboard from './Dashboard';
import { ConfirmButton } from './Button';
import _ from 'lodash';
import fuzzy from 'fuzzy';
import moment from 'moment';
import axios from 'axios';

global.fetch = require('node-fetch')

const AppLayout = styled.div`
  padding: 40px;
`;

const Content = styled.div``;
export const CenterDiv = styled.div`
  display: grid;
  justify-content: center;
`;

const MAX_FAVORITES = 10;
const TIME_UNITS = 10;
const ccapi = '823e157901a82b0ec0538e4d36a8f8c13317a2e3af79edb868318fc4bbf74501';

const checkFirstVisit = () => {
  let cryptoDashData = JSON.parse(localStorage.getItem('cryptoDash'));
  if (!cryptoDashData) {
    return {
      firstVisit: true,
      page: 'settings'
    };
  }
  let { favorites, currentFavorite } = cryptoDashData;
  return {
    favorites,
    currentFavorite
  };
};

class App extends Component {
  state = {
    page: 'dashboard',
    favorites: ['BTC', 'ETH', 'ADA', 'NEM'],
    timeInterval: 'months',
    ...checkFirstVisit()
  };

  componentDidMount = () => {
    this.fetchHistorical();
    this.fetchCoins();
    this.fetchPrices();
  };

  validateFavorites = coinList => {
    let validatedFavorites = [];
    this.state.favorites.forEach(favorite => {
      if (coinList[favorite]) {
        validatedFavorites.push(favorite);
      }
    });
    return validatedFavorites;
  };

  fetchCoins = async () => {    
    //let coinList1 = ( await cc.coinList()).Data;
    let coinList = {};

    const res = await axios.get(`https://min-api.cryptocompare.com/data/top/mktcapfull?limit=100&tsym=USD&api_key=${ccapi}`);

    res.data.Data.forEach(coin => {
        let cobj = {};
        cobj.CoinName = coin.CoinInfo.FullName;
        cobj.Symbol = coin.CoinInfo.Name;
        cobj.ImageUrl = coin.CoinInfo.ImageUrl;

        coinList[coin.CoinInfo.Name] = cobj;
    })
      
    this.setState({ coinList, favorites: this.validateFavorites(coinList) });
    
  };
  
  fetchPrices = async () => {
    if (this.state.firstVisit) return;
    let prices = await this.prices();

    //debugger
    this.setState({ prices });
  };

  fetchHistorical = async () => {
    if (this.state.firstVisit) return;
    let results = await this.historical();
    
    let historical = [
      {
        name: this.state.currentFavorite,
        data: results.map((ticker, index) => [
          moment()
            .subtract({ [this.state.timeInterval]: TIME_UNITS - index })
            .valueOf(),
          ticker.USD
        ])
      }
    ];
    this.setState({ historical });
  };

  historical = async () => {

    let tstamp = 0;
    let prices = [];
    const token = this.state.currentFavorite;

    for (let units = TIME_UNITS; units > 0; units--) {
      tstamp = moment().subtract({ [this.state.timeInterval]: units }).unix();

      const res = await axios.get(`https://min-api.cryptocompare.com/data/pricehistorical?fsym=${token}&tsyms=USD&ts=${tstamp}&api_key=${ccapi}`);

      const pr = res.data[token].USD;
      prices.push({'USD': pr});
    }

    return prices
  };

  prices = async () => {
    let returnData = [];
    for (let i = 0; i < this.state.favorites.length; i++) {
      const token = this.state.favorites[i];
      try {
        const res = await axios.get(`https://min-api.cryptocompare.com/data/pricemultifull?fsyms=${token}&tsyms=USD&api_key=${ccapi}`);
        
        const myObj = {};
        myObj[token] = {};
        myObj[token]['USD'] = {};
        myObj[token]['USD']['PRICE'] = res.data.RAW[token].USD.PRICE;
        myObj[token]['USD']['CHANGEPCT24HOUR'] = res.data.RAW[token].USD.CHANGEPCT24HOUR;
        
        returnData.push(myObj);

      } catch (e) {
        console.warn('Fetch price error: ', e);
      }
    }
    return returnData;
  };

  displayingDashboard = () => this.state.page === 'dashboard';
  displayingSettings = () => this.state.page === 'settings';

  firstVisitMessage = () => {
    if (this.state.firstVisit) {
      return (
        <div>
          Welcome to CryptoDash, please select your favorite coins to begin.{' '}
        </div>
      );
    }
  };

  confirmFavorites = () => {
    let currentFavorite = this.state.favorites[0];
    this.setState(
      {
        firstVisit: false,
        page: 'dashboard',
        prices: null,
        currentFavorite,
        historical: null
      },
      () => {
        this.fetchPrices();
        this.fetchHistorical();
      }
    );

    localStorage.setItem(
      'cryptoDash',
      JSON.stringify({
        favorites: this.state.favorites,
        currentFavorite
      })
    );
  };

  //{Search.call(this)}
  settingsContent = () => {
    return (
      <div>
        {this.firstVisitMessage()}
        <div>
          {CoinList.call(this, true)}
          <CenterDiv>
            <ConfirmButton onClick={this.confirmFavorites}>
              Save Favorites
            </ConfirmButton>
          </CenterDiv>
          
          {CoinList.call(this)}
        </div>
      </div>
    );
  };

  loadingContent = () => {
    if (!this.state.coinList) {
      return <div> Loading Coins </div>;
    }
    if (!this.state.firstVisit && !this.state.prices) {
      return <div> Loading Prices </div>;
    }
  };

  addCoinToFavorites = key => {
    let favorites = [...this.state.favorites];
    if (favorites.length < MAX_FAVORITES) {
      favorites.push(key);
      this.setState({ favorites });
    }
  };

  removeCoinFromFavorites = key => {
    let favorites = [...this.state.favorites];
    this.setState({ favorites: _.pull(favorites, key) });
  };

  isInFavorites = key => _.includes(this.state.favorites, key);

  handleFilter = _.debounce(inputValue => {
    // Get all the coin symbols
    let coinSymbols = Object.keys(this.state.coinList);
    // Get all the coin names, maps symbol to name
    let coinNames = coinSymbols.map(sym => this.state.coinList[sym].CoinName);
    let allStringsToSearch = coinSymbols.concat(coinNames);
    let fuzzyResults = fuzzy
      .filter(inputValue, allStringsToSearch, {})
      .map(result => result.string);

    let filteredCoins = _.pickBy(this.state.coinList, (result, symKey) => {
      let coinName = result.CoinName;
      // If our fuzzy results contains this symbol OR the coinName, include it (return true).
      return (
        _.includes(fuzzyResults, symKey) || _.includes(fuzzyResults, coinName)
      );
    });

    this.setState({ filteredCoins });
  }, 500);

  filterCoins = e => {
    let inputValue = _.get(e, 'target.value');
    if (!inputValue) {
      this.setState({
        filteredCoins: null
      });
      return;
    }
    this.handleFilter(inputValue);
  };

  render() {
    return (
      <AppLayout>
        {AppBar.call(this)}
        {this.loadingContent() || (
          <Content>
            {this.displayingSettings() && this.settingsContent()}
            {this.displayingDashboard() && Dashboard.call(this)}
          </Content>
        )}
      </AppLayout>
    );
  }
}

export default App;



